<template>
  <div class="page-container">
    <base-breadcrumbs
      :parents="[['Home','/']]"
      :currentLocation="'Producten'"
    />
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>
          <base-icon
            iconRef="grid"
            position="left"
          />Producten
        </h2>
      </div>
      <div class="page-box-datatable-container">
        <div class="data-table-wrapper">
          <table class="page-datatable">
            <thead>
              <tr>
                <th>Categorie</th>
                <th>SKU</th>
                <th>Productnaam</th>
                <th class="align-right">Prijs</th>
              </tr>
            </thead>
            <tr
              v-for="product in productsList"
              :key="product.id"
            >
              <td>{{product.field_365}}</td>
              <td>{{product.field_127}}</td>
              <td>{{product.field_125}}</td>
              <td class="align-right">{{product.field_384_raw | currencyFilter}}</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      placeholderImgUrl: "https://dummyimage.com/600x338/009ad9/009ad9&text=+",
      fieldMap: {
        productNaam: "field_125",
        merk: "field_126",
        sku: "field_127",
        category: "field_365",
        beschrijving: "field_366",
        prijs: "field_378_raw"
      }
    };
  },
  computed: {
    productsList: function() {
      return this.$store.getters.getProductsList;
    },
    filteredProductsList: function() {
      const filteredProductsList = [];
      this.productsList.forEach(product => {
        filteredProductsList.push({
          active: product.field_364,
          productName: product.field_125,
          brand: product.field_126,
          sku: product.field_127,
          category: product.field_365,
          width: product.field_374_raw,
          length: product.field_375_raw,
          ac: [
            product.field_368_raw,
            product.field_369_raw,
            product.field_370_raw,
            product.field_371_raw,
            product.field_372_raw,
            product.field_373_raw
          ],
          description: product.field_366,
          priceEach: product.field_378_raw,
          pricePerM2: product.field_384_raw,
          imgUrl: product.field_385_raw.url
        });
      });
      return filteredProductsList;
    },
    filteredProductsSortedByCategory: function() {
      const uniqueCategories = [
        ...new Set(this.filteredProductsList.map(item => item.category))
      ];
      const filteredProductsSortedByCategory = [];
      uniqueCategories.forEach((category, categoryIndex) => {
        filteredProductsSortedByCategory.push(
          this.filteredProductsList.filter(product => {
            return product.category == uniqueCategories[categoryIndex];
          })
        );
      });
      return filteredProductsSortedByCategory;
    }
  },
  filters: {
    currencyFilter: function(value) {
      return "â‚¬ " + value.toFixed(2);
    }
  },
  created: function() {
    this.$setPageTitle("Producten");
  }
};
</script>

<style scoped>
th,
td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
td {
  font-size: 14px !important;
}
.align-right {
  text-align: right;
}
</style>



